@import url(https://fonts.googleapis.com/css2?family=Lora:wght@400;600;700&family=Noto+Sans:wght@400;700&family=Open+Sans:wght@300;400;700&family=Roboto+Slab:wght@100;300;400;500;600;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,400&display=swap);
:root{--cell-size: 2vmin;--cell-color: #d7d7d7;--cell-food-color: #42bd42;--cell-snake-color: #312f2f;--regular-font: Roboto, 'Open Sans', sans-serif;--header-font: 'Roboto Slab', 'Lora', 'Noro Sans', serif;font-family:Roboto, 'Open Sans', sans-serif;font-family:var(--regular-font);box-sizing:border-box;font-weight:normal}@media screen and (max-width: 720px){:root{--cell-size: 3vmin}}*,*::after,*::before{box-sizing:inherit}body{margin:0;-ms-scroll-chaining:none;overscroll-behavior:contain}body *+*{margin-top:1.5em}#root{margin-top:0}.App{display:flex;flex-flow:column;justify-content:flex-start;align-items:center}.App .app-field{margin:1rem 0}.App .app-score{display:flex}.App .app-score *{flex:1 0;margin-top:0}.App .app-score>*{border:1px black solid;display:flex;flex-flow:column}.App .app-score>*:nth-child(n+2){border-left:none}.App .app-score label{border-bottom:1px solid black}.App .start-game{align-self:stretch;padding:0.5rem;margin:1rem 1rem}.App .app-controller{flex:0 0 auto;margin-top:4rem}

.app-field{height:calc(var(--cell-size) * 31);width:calc(var(--cell-size) * 31);display:grid;grid-template-rows:repeat(31, var(--cell-size));grid-template-columns:repeat(31, var(--cell-size))}.app-field .app-cell{display:block;height:var(--cell-size);width:var(--cell-size);background-color:var(--cell-color);margin-top:0}.app-field .app-cell.food-cell{background-color:var(--cell-food-color)}.app-field .app-cell.snake-cell{background-color:var(--cell-snake-color)}

.app-controller{position:relative;left:5vmin;display:grid;grid-template-columns:repeat(3, 15vmin);grid-template-rows:repeat(3, 15vmin)}.app-controller .control{margin-top:0;border:0.75rem solid #312f2f;height:1.5rem;width:1.5rem}.app-controller .arrow{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.app-controller .app-controller__top{grid-row:1;grid-column-start:2;grid-column-end:3;border-bottom-width:0;border-right-width:0}.app-controller .app-controller__right{grid-row:2;grid-column-start:3;grid-column-end:4;border-left-width:0;border-bottom-width:0}.app-controller .app-controller__bottom{grid-row:3;grid-column-start:2;grid-column-end:3;border-left-width:0;border-top-width:0}.app-controller .app-controller__left{grid-row:2;grid-column-start:1;grid-column-end:2;border-right-width:0;border-top-width:0}.app-controller .app-controller__cursor{border-radius:0.75rem}.app-controller .app-controller__cursor-placeholder{margin-top:0;grid-row:2;grid-column-start:2;grid-column-end:3}@media screen and (min-width: 728px){.app-controller{display:none}}.app-controller.up .app-controller__top{border-color:orange}.app-controller.up .app-controller__cursor{-webkit-transform:translate(0, -0.75rem);transform:translate(0, -0.75rem)}.app-controller.down .app-controller__bottom{border-color:orange}.app-controller.down .app-controller__cursor{-webkit-transform:translate(0, 0.75rem);transform:translate(0, 0.75rem)}.app-controller.right .app-controller__right{border-color:orange}.app-controller.right .app-controller__cursor{-webkit-transform:translate(0.75rem, 0);transform:translate(0.75rem, 0)}.app-controller.left .app-controller__left{border-color:orange}.app-controller.left .app-controller__cursor{-webkit-transform:translate(-0.75rem, 0);transform:translate(-0.75rem, 0)}

